.slider-desc {
  cursor: pointer;
  border: 2px solid var(--color-text-black);
  -webkit-transition: var(--anim-mid);
  transition: var(--anim-mid);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 34px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

