/* ======== FROM HERO CSS ========= */

@media screen and (min-width: 768px) {
  .hero-wrapper {
    display: flex;
    flex-direction: row;
    gap: 27px;
  }
}
@media only screen and (min-width: 1280px) {
  .hero-wrapper {
    gap: 145px;
  }
}

.main-title {
  max-width: 335px;
  margin-top: 75px;
  margin-bottom: 18px;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.19;
}
@media screen and (min-width: 768px) {
  .main-title {
    max-width: 384px;
    margin-top: 194px;
    margin-bottom: 24px;
    font-size: 44px;
    line-height: 1.09;
  }
}
@media only screen and (min-width: 1280px) {
  .main-title {
    max-width: 555px;
    font-size: 64px;
    margin-top: 164px;
    margin-bottom: 28px;
  }
}
.text-violet {
  color: var(--color-violet);
  font-style: italic;
}
.main-text {
  max-width: 335px;
  margin-bottom: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.43;
}
@media screen and (min-width: 768px) {
  .main-text {
    max-width: 338px;
    margin-bottom: 48px;
    font-size: 16px;
  }
}
@media only screen and (min-width: 1280px) {
  .main-text {
    max-width: 478px;
    font-size: 20px;
    margin-bottom: 56px;
    line-height: 1.3;
  }
}
.search-button {
  display: flex;
  align-items: center;
  width: 178px;
  height: 48px;
  padding: 14px 28px;
  border-radius: 12px;
  background-color: var(--color-violet);
  text-decoration: none;

  color: var(--color-background-white);
  font-style: normal;
  font-weight: 600;
  line-height: 1.43;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.search-button:hover {
  background-color: var(--color-button-hover);
}
@media screen and (min-width: 768px) {
  .search-button {
    width: 245px;
    height: 68px;
    padding: 22px 44px;
    font-size: 18px;
    line-height: 1.33;
  }
}
.hero-back-img {
  width: 230px;
  height: 338px;
  margin: 76px auto 40px;
  border-radius: 80px 0px;
  background: var(--color-violet);
  box-shadow: 0px 28px 75px 0px rgba(88, 109, 204, 0.5);
  position: relative;
}
@media screen and (min-width: 768px) {
  .hero-back-img {
    width: 293px;
    height: 417px;
    margin-top: 148px;
    margin-bottom: 51px;
  }
}
@media screen and (min-width: 1280px) {
  .hero-back-img {
    width: 380px;
    height: 476px;
    margin-top: 128px;
    margin-bottom: 51px;
  }
}
.hero-img {
  position: absolute;
  top: 321px;
  left: 0;
}
@media screen and (max-width: 374px) {
  .hero-img {
    width: 320px;
    top: 321px;
  }
}
@media screen and (min-width: 375px) {
  .hero-img {
    width: 334px;
    height: 367px;
    top: 303px;
    left: 20px;
  }
}
@media screen and (min-width: 768px) {
  .hero-img {
    width: 333px;
    height: 451px;
    top: 114px;
    left: 403px;
  }
}
@media screen and (min-width: 1280px) {
  .hero-img {
    width: 410px;
    height: 565px;
    top: 39px;
    left: 770px;
  }
}
.hero-container .hero-search-text {
  width: 173px;
  margin-left: 61px;
  margin-right: 61px;
  margin-bottom: 40px;

  font-style: normal;
  font-weight: 400;
  line-height: 1.43;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .hero-container .hero-search-text {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;
    width: 198px;
    font-size: 16px;
    line-height: 1.5;
  }
}
@media screen and (min-width: 1280px) {
  .hero-container .hero-search-text {
    width: 219px;
    margin-bottom: 132px;
    font-size: 18px;
    line-height: 1.33;
  }
}
.hero-select {
  width: 90px;
  height: 34px;
  padding: 8px 14px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 80px;
  border-radius: 8px;
  background: rgba(126, 143, 221, 0.4);

  display: flex;
  gap: 33px;
  color: var(--color-background-white);
  border: none;
}
.hero-select:hover,
.hero-select:focus {
  background-color: var(--color-turquoise);
}
@media screen and (min-width: 768px) {
  .hero-select {
    display: none;
  }
}

.hero-select-items {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 100px;
}

@media screen and (max-width: 767px) {
  .hero-select-items {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .hero-select-items {
    margin-left: 88px;
    margin-right: 88px;
  }
}

.hero-select-items > button {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  color: var(--color-background-white);
  background: rgba(126, 143, 221, 0.4);
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
}

.hero-select-btn:hover,
.hero-select-btn:focus {
  background-color: var(--color-turquoise);
}
